import React from 'react'
import Link from 'gatsby-link'

import { Container, Col, Row } from 'reactstrap'
import styled from 'styled-components'

import IndexSection from '../components/IndexSection'

const BodyContainer = styled(Container)`
  background-color: #000000;
  font-family: 'Ubuntu', sans-serif;
`

const IndexPage = () => (
  <BodyContainer className="p-5" fluid={true}>
    <IndexSection
      header="MISSION"
      text="To create meaningful experiences for others through visual design, narrative structure, and the unique pairing of the two."
    />
    <IndexSection
      header="RECENT"
      text={
        <div>
          Projects for{' '}
          <a target="__blank" href="https://www.truebase.io">
            Truebase
          </a>,{' '}
          <a target="__blank" href="https://www.azuqua.com">
            Azuqua
          </a>, and{' '}
          <a target="__blank" href="https://www.jump2go.com">
            Jump2Go
          </a>.
        </div>
      }
    />
    <IndexSection
      header="PROJECTS"
      text="11 websites, 9 apps, 40 videos, 15 articles, and 1 game."
    />
    <IndexSection
      header="FEATURED"
      text={
        <div>
          <a
            target="__blank"
            href="https://techcrunch.com/2016/12/04/wandered-space/"
          >
            wandered.space
          </a>,{' '}
          <a
            target="__blank"
            href="https://azuqua.com/2016/11/mission-accomplished-flo-force-one"
          >
            FLO Force One
          </a>,{' '}
          <a
            target="__blank"
            href="https://s3-us-west-2.amazonaws.com/skyh-public-assets/Hope's_End_Rules.pdf"
          >
            Hope's End
          </a>,{' '}
          <a target="__blank" href="https://skylerhartle.itch.io/new-heights">
            New Heights
          </a>,{' '}
          <a target="__blank" href="https://calm-river-4553.herokuapp.com/">
            Weatherwords
          </a>,{' '}
          <a
            target="__blank"
            href="http://www.stayalert.org"
          >
            StayAlert
          </a>
        </div>
      }
    />
    <IndexSection
      header="AWARDS"
      text="2016 GE Minds & Machines $25,000 Grand Prize, 2016 TechCrunch Disrupt London Hackathon Runner-Up, 2015 RadioWorld Best of Show Award"
    />
    <IndexSection
      header="WHAT I DO"
      text="Software Development, Creative Writing, Technical Writing, Scriptwriting, Strategy, Content Development, Narrative Design, Game Design, Narrative Direction, User Interface and Experience, Web Design"
    />
  </BodyContainer>
)

export default IndexPage



